/* <icon-font.scss> */

.fa,
[data-icon]:before,
[class^="icon-"]:before,
[class*=" icon-"]:before,
.ion,
.ionicons,
%ion-after,
[class^="ion-"]:before,
[class*=" ion-"]:before {
  display: inline-block;
  font-size: inherit;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fa {
    font-family: "FontAwesome";
}
[data-icon]:before,
[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "linea";
}
.ion,
.ionicons,
%ion-after,
[class^="ion-"]:before,
[class*=" ion-"]:before {
    font-family: "Ionicons";
}
@-webkit-keyframes fa-spin {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(359deg);
  }
}
@keyframes fa-spin {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(359deg);
  }
}

